body {
    background-color: #efefef;
}

a {
    color: #000000;
}

button {
    border: none;
    background-color: inherit;
}

.container {
    max-width: 600px;
    margin: 0 auto;
    min-height: 100%;
    padding-bottom: 50px;
    max-height: 1000px;
}

.header-container {
    height: 30px;
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.hero-container {
    position: relative;
    text-align: center;
    color: white;
}

.hero {
    width: 100%;
    height: auto;
    -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
            box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.footer-container {
    height: 70px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    max-width: 600px;
    width: 600px;
    bottom: 0;
}

@media only screen and (max-width: 600px) {
    .footer-container {
        width: 100%;
    }
}



